<template>
	<nav class="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow">
		<!-- Sidebar Toggle (Topbar) -->
		<button id="sidebarToggleTop" class="btn btn-link d-md-none rounded-circle mr-3">
			<i class="fa fa-bars"></i>
		</button>
		
		<!-- Topbar Navbar -->
		<ul class="navbar-nav ml-auto">
			
			<!-- Nav Item - Alerts -->
			<li class="nav-item dropdown no-arrow mx-1">
				<a
					class="nav-link dropdown-toggle"
					href="#"
					id="alertsDropdown"
					role="button"
					data-toggle="dropdown"
					aria-haspopup="true"
					aria-expanded="false"
				>
					<i class="fas fa-bell fa-fw"></i>
					<!-- Counter - Alerts -->
					<span class="badge badge-danger badge-counter">3+</span>
				</a>
				<!-- Dropdown - Alerts -->
				<div
					class="dropdown-list dropdown-menu dropdown-menu-right shadow animated--grow-in"
					aria-labelledby="alertsDropdown"
				>
					<h6 class="dropdown-header">Alerts Center</h6>
					<a v-for="(alert, index) in alerts" :key="index" class="dropdown-item d-flex align-items-center" href="#">
						<div class="mr-3">
							<div class="icon-circle bg-primary">
								<i class="fas text-white" :class="[getIcon(alert.icon)]"></i>
							</div>
						</div>
						<div>
							<div class="small text-gray-500">{{ alert.create_at }}</div>
							<span class="font-weight-bold">{{ alert.content }}</span>
						</div>
					</a>
					
					
					<a class="dropdown-item text-center small text-gray-500" href="#">Show All Alerts</a>
				</div>
			</li>
		</ul>
	</nav>
</template>

<script>
	export default {
		data() {
			return {
				alerts: [
					{
						content: "A new monthly report is ready to download!",
						create_at: "December 12, 2019",
						icon: "1"
					},
					{
						content: "$290.29 has been deposited into your account!",
						create_at: "December 7, 2019",
						icon: "2"
					},
					{
						content: "Spending Alert: We've noticed unusually high spending for your account.",
						create_at: "December 2, 2019",
						icon: "3"
					}
				]
			}
		},
		methods: {
			getIcon(iconId) {
				return iconId == 1 ? "fa-file-alt" : (iconId == 2 ? "fa-donate" : "fa-exclamation-triangle" );
			}
		},
		mounted() {
			document.querySelector('#sidebarToggleTop').click()
		},
	}
</script>



