<template>
	<div class="card shadow mb-4">
		<div class="card-header py-3">
			<h6 class="m-0 font-weight-bold text-primary">Confession mi cp nht</h6>
		</div>
		<div class="card-body">
			<div class="table-responsive">
				<div id="dataTable_wrapper" class="dataTables_wrapper dt-bootstrap4">
					<div class="row">
						<div class="col-sm-12">
							<table
								class="table table-bordered dataTable"
								id="dataTable"
								width="100%"
								cellspacing="0"
								role="grid"
								aria-describedby="dataTable_info"
								style="width: 100%;"
							>
								<thead>
									<tr role="row">
										<th
											class="sorting_asc"
											tabindex="0"
										><input @change="selectAll" type="checkbox"/></th>
										<th
											class="sorting"
											tabindex="0"
											aria-controls="dataTable"
										>Ni dung</th>
									</tr>
								</thead>
								<tbody>
									<tr role="row" class="odd" v-for="(confession, index) in confessions" :key="index">
										<td><input type="checkbox" :value="confession._id" v-model="selectedConfession"></td>
										<td>#{{ confession._id }}: {{ confession.content }}</td>
									</tr>
								</tbody>
							</table>
						</div>
					</div>
				</div>
			</div>
			<div class="d-flex flex-row-reverse">
				<button @click="acceptConfession" class="btn btn-success btn-circle mt-4 mr-2">
					<i class="fas fa-check"></i>
				</button>
				<button @click="deleteConfession" class="btn btn-danger btn-circle mt-4 mr-2">
					<i class="fas fa-trash"></i>
				</button>
			</div>
			
		</div>
	</div>
</template>

<script>
	
	export default {
		data() {
			return {
				confessions: [],
				selectedConfession: []
			}
		},
		methods: {
			getConfession() {
				console.log("Get confession")
				Axios.get('/confession/new')
					.then(res => {
						this.confessions = res.data.data;
					})
					.catch(err => {
						console.log(err);
					});
			},
			acceptConfession() {
				Axios.put('/confession', {
					id: this.selectedConfession
				}).then(confession => {
					this.getConfession();
				}).catch(err => {
					console.log(err)
				})
			},
			deleteConfession() {
				Axios.delete('/confession', {
					 data: {
						id: this.selectedConfession
					}
				}).then(confession => {
					this.getConfession();
				}).catch(err => {
					console.log(err)
				})
			},
			selectAll() {
				if(this.confessions.length > this.selectedConfession.length) {
					this.confessions.forEach(confession => {
						this.selectedConfession.push(confession._id);
					});
				} else {
					this.selectedConfession = []
				}
				
			}
		},
		created() {
			
			this.getConfession();
		}
	}
</script>


