	<template>
	<div class="bg-gradient-primary">
		<div class="container">
			<!-- Outer Row -->
			<div class="row justify-content-center">
				<div class="col-xl-10 col-lg-12 col-md-9">
					<div class="card o-hidden border-0 shadow-lg my-5">
						<div class="card-body p-0">
							<!-- Nested Row within Card Body -->
							<div class="row">
								<div class="col-lg-6 d-none d-lg-block bg-login-image"></div>
								<div class="col-lg-6">
									<div class="p-5">
										<div class="text-center">
											<h1 class="h4 text-gray-900 mb-4">Welcome Back!</h1>
										</div>
										<form @click="$event.preventDefault()" class="user">
											<div class="form-group">
												<input
													type="text"
													class="form-control form-control-user"
													v-model="user.name"
													placeholder="Enter Username..."
												/>
											</div>
											<div class="form-group">
												<input
													type="password"
													class="form-control form-control-user"
													v-model="user.password"
													placeholder="Password"
												/>
											</div>
											<div class="form-group">
												<div class="custom-control custom-checkbox small">
													<input type="checkbox" class="custom-control-input" id="customCheck" />
													<label class="custom-control-label" for="customCheck">Remember Me</label>
												</div>
											</div>
											<button @click="login" class="btn btn-primary btn-user btn-block">Login</button>
											<hr />
											<button class="btn btn-google btn-user btn-block">
												<i class="fab fa-google fa-fw"></i> Login with Google
											</button>
											<button class="btn btn-facebook btn-user btn-block">
												<i class="fab fa-facebook-f fa-fw"></i> Login with Facebook
											</button>
										</form>
										<hr />
										<div class="text-center">
											<a class="small" href="forgot-password.html">Forgot Password?</a>
										</div>
										<div class="text-center">
											<a class="small" href="register.html">Create an Account!</a>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				user: {
					name: "",
					password: ""			
				}
			}
		},
		methods: {
			login(e) {
				e.preventDefault()
				Axios.post('	/login', {
					name: this.user.name,
					password: this.user.password})
					.then(res => {
						if(res.data.success) {
							this.loginSuccessAlert();
							localStorage.setItem('jwt', res.data.token);
							localStorage.setItem('user', res.data.user);
							this.$router.push({ name: 'Home' });
						} else {
							this.loginFailAlert();
						}
					})
					.catch(err => {
						console.log(err)
				});
			},
			loginFailAlert() {
				Swal.fire({
					type: 'error',
					title: 'Oops...',
					text: 'C g  sai!',
					footer: '<router-view to="/login">Qun mt khu chng?</router-view>'
				});
			},
			loginSuccessAlert() {
				const Toast = Swal.mixin({
					toast: true,
					position: 'top-end',
					showConfirmButton: false,
					timer: 1500
				});

				Toast.fire({
					type: 'success',
					title: 'Signed in successfully'
				})
			}
		}
	}
</script>


