import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import errorPage from '../components/404'
import OldConfession from '../components/OldConfession'
import Login from '../components/Login'
import User from '../components/User'

Vue.use(Router)

export default new Router({
	mode: 'history',
	routes: [
		{
			path: '/home',
			name: 'Home',
			component: Home,
            meta: { 
                requiresAuth: true
            }
		},
		{
			path: '/old',
			name: 'OldConfession',
			component: OldConfession,
            meta: { 
                requiresAuth: true
            }
		},
		{
			path: '/login',
			name: 'Login',
			component: Login,
            meta: { 
                guest: true,
				nonLayout: true
            }
		},
		{
			path: '/user',
			name: 'User',
			component: User,
            meta: { 
				requiresAuth: true
            }
		},
		{
			path: '*',
			name: 'errorPage',
			component: errorPage,
            meta: { 
                requiresAuth: true
            }
		}
	]
});







